import React from "react";
import Image from "next/image";
import style from "./blogPreview.module.css";
import Link from "next/link";
import type { Blog } from "@/database/blogSchema";

export default function BlogPreview(props: Blog) {
  const formattedDate = new Date(props.date).toLocaleDateString("en-US", {
    year: "numeric",
    month: "long",
    day: "numeric",
  });

  return (
    <div className={style.div}>
      {/* <Link href={`/blog/${props.slug}`}> */}
      <h3 className={style["blog-title"]}>{props.title}</h3>
      <div className={style["blog-img"]}>
        {/* <Image src={props.image} alt={props.title} width={200} height={200} /> */}
        <Image
          src={props.image}
          alt={props.title}
          width={props.width}
          height={props.height}
          //   unoptimized
        />
      </div>
      <p className={style["blog-description"]}>{props.description}</p>
      <p className={style["blog-description"]}>Posted on: {formattedDate}</p>
      {/* </Link> */}
    </div>
  );
}
